.btn {
  margin-top: 3rem;
}
.btn a, .btn button, .btn span, .btn input {
  display: flex;
  width: fit-content;
  max-width: 300px;
  padding: 0.4em 1em;
  font-size: 1.3rem;
  transition: 0.3s;
  letter-spacing: 0.1em;
  color: #49b9e1;
  border: 2px solid #8bd2ec;
  background: #fff;
  justify-content: center;
  align-items: center;
}
.btn a::after, .btn button::after, .btn span::after, .btn input::after {
  content: "";
  position: relative;
  display: block;
  width: 0.4em;
  height: 0.4em;
  margin-left: 0.5em;
  transition: 0.3s;
  transform: rotate(45deg);
  border-top: 1px solid #8bd2ec;
  border-right: 1px solid #8bd2ec;
}
.btn a:hover, .btn button:hover, .btn input:hover {
  color: #fff;
  background: #8bd2ec;
}
.btn a:hover::after, .btn button:hover::after, .btn input:hover::after {
  transform: translateX(0.2em) rotate(45deg);
  border-color: #fff;
}
.btn span {
  color: #ccc;
  border-color: #ddd;
}
.btn span::after {
  border-color: #ddd;
}
.btn.-input {
  position: relative;
}
.btn.-input::after {
  content: "";
  position: relative;
  position: absolute;
  top: 50%;
  right: 1em;
  display: block;
  width: 0.4em;
  height: 0.4em;
  transition: 0.3s;
  transform: translate(0, -50%) rotate(45deg);
  border-top: 1px solid #8bd2ec;
  border-right: 1px solid #8bd2ec;
}
.btn.-input:hover::after {
  transform: translate(0.2em, -50%) rotate(45deg);
  border-color: #fff;
}
.btn.-input input {
  padding-right: 2em;
}
.btn.-back a, .btn.-back button, .btn.-back span {
  flex-direction: row-reverse;
}
.btn.-back a::after, .btn.-back button::after, .btn.-back span::after {
  margin-right: 0.5em;
  margin-left: 0;
  transform: rotate(-135deg);
}
.btn.-back a:hover::after, .btn.-back button:hover::after {
  transform: translateX(-0.2em) rotate(-135deg);
  border-color: #fff;
}

