.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
  gap: 1em;
  list-style-type: none;
}
.pagination a, .pagination span {
  display: flex;
  width: 4rem;
  height: 4rem;
  color: #49b9e1;
  border: 1px solid #8bd2ec;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
}
.pagination a:hover {
  border-width: 3px;
}
.pagination span {
  color: #fff;
  background: #8bd2ec;
}
.pagination .prev,
.pagination .next,
.pagination .first,
.pagination .last {
  overflow: hidden;
}
.pagination .prev a, .pagination .prev span,
.pagination .next a,
.pagination .next span,
.pagination .first a,
.pagination .first span,
.pagination .last a,
.pagination .last span {
  position: relative;
  text-indent: 100rem;
  color: transparent;
}
.pagination .prev a::before, .pagination .prev a::after, .pagination .prev span::before, .pagination .prev span::after,
.pagination .next a::before,
.pagination .next a::after,
.pagination .next span::before,
.pagination .next span::after,
.pagination .first a::before,
.pagination .first a::after,
.pagination .first span::before,
.pagination .first span::after,
.pagination .last a::before,
.pagination .last a::after,
.pagination .last span::before,
.pagination .last span::after {
  content: "";
  position: absolute;
  top: 50%;
  display: block;
  width: 20%;
  height: 20%;
  border-top: 1px solid #49b9e1;
  border-right: 1px solid #49b9e1;
}
.pagination .prev span,
.pagination .next span,
.pagination .first span,
.pagination .last span {
  border-color: #ddd;
  background: #fff;
}
.pagination .prev span::before, .pagination .prev span::after,
.pagination .next span::before,
.pagination .next span::after,
.pagination .first span::before,
.pagination .first span::after,
.pagination .last span::before,
.pagination .last span::after {
  border-color: #ddd;
}
.pagination .prev a::before, .pagination .prev span::before,
.pagination .next a::before,
.pagination .next span::before {
  left: 38%;
  transform: translateY(-50%) rotate(45deg);
}
.pagination .prev a::after, .pagination .prev span::after,
.pagination .next a::after,
.pagination .next span::after {
  display: none;
}
.pagination .first a::before, .pagination .first span::before,
.pagination .last a::before,
.pagination .last span::before {
  left: 30%;
  transform: translateY(-50%) rotate(45deg);
}
.pagination .first a::after, .pagination .first span::after,
.pagination .last a::after,
.pagination .last span::after {
  left: 48%;
  transform: translateY(-50%) rotate(45deg);
}
.pagination .prev,
.pagination .first {
  transform: rotate(180deg);
}

