.contactTable input[type=text],
.contactTable input[type=tel],
.contactTable input[type=email],
.contactTable textarea {
  width: 100%;
  padding: 0.5em;
  background: #f3f3f3;
}
@supports (-webkit-appearance: none) or (-moz-appearance: none) {
  .contactTable input[type=checkbox],
.contactTable input[type=radio] {
    --active: #8bd2ec;
    --active-inner: #fff;
    --focus: 2px #8bd2ecaa;
    --border: #c5c5c5;
    --border-hover: #8bd2ec;
    --background: #fff;
    --disabled: #e7e7e7;
    --disabled-inner: #8bd2ec;
    position: relative;
    display: inline-block;
    width: 18px;
    min-width: 18px;
    height: 18px;
    margin: 0;
    cursor: pointer;
    transition: background 0.3s, border-color 0.3s, box-shadow 0.2s;
    border: 1px solid var(--bc, var(--border));
    outline: none;
    background: var(--b, var(--background));
    -webkit-appearance: none;
    -moz-appearance: none;
  }
  .contactTable input[type=checkbox]:after,
.contactTable input[type=radio]:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    transition: transform var(--d-t, 0.3s) var(--d-t-e, ease), opacity var(--d-o, 0.2s);
  }
  .contactTable input[type=checkbox]:checked,
.contactTable input[type=radio]:checked {
    --b: var(--active);
    --bc: var(--active);
    --d-o: .3s;
    --d-t: .6s;
    --d-t-e: cubic-bezier(.2, .85, .32, 1.2);
  }
  .contactTable input[type=checkbox]:disabled,
.contactTable input[type=radio]:disabled {
    --b: var(--disabled);
    cursor: not-allowed;
    opacity: 0.9;
  }
  .contactTable input[type=checkbox]:disabled:checked,
.contactTable input[type=radio]:disabled:checked {
    --b: var(--disabled-inner);
    --bc: var(--border);
  }
  .contactTable input[type=checkbox]:disabled + label,
.contactTable input[type=radio]:disabled + label {
    cursor: not-allowed;
  }
  .contactTable input[type=checkbox]:hover:not(:checked):not(:disabled),
.contactTable input[type=radio]:hover:not(:checked):not(:disabled) {
    --bc: var(--border-hover);
  }
  .contactTable input[type=checkbox]:focus,
.contactTable input[type=radio]:focus {
    box-shadow: 0 0 0 var(--focus);
  }
  .contactTable input[type=checkbox]:not(.switch):after,
.contactTable input[type=radio]:not(.switch):after {
    opacity: var(--o, 0);
  }
  .contactTable input[type=checkbox]:not(.switch):checked,
.contactTable input[type=radio]:not(.switch):checked {
    --o: 1;
  }
  .contactTable input[type=checkbox]:not(.switch):checked + label,
.contactTable input[type=radio]:not(.switch):checked + label {
    color: #333;
  }
  .contactTable input[type=checkbox] + label,
.contactTable input[type=radio] + label {
    margin-left: 0.5em;
    cursor: pointer;
  }
  .contactTable input[type=checkbox]:not(.switch) {
    border-radius: 4px;
  }
  .contactTable input[type=checkbox]:not(.switch):after {
    top: 3px;
    left: 5px;
    width: 6px;
    height: 9px;
    transform: rotate(var(--r, 20deg));
    border: 2px solid var(--active-inner);
    border-top: 0;
    border-left: 0;
  }
  .contactTable input[type=checkbox]:not(.switch):checked {
    --r: 43deg;
  }
  .contactTable input[type=radio] {
    border-radius: 50%;
  }
  .contactTable input[type=radio]:after {
    width: 16px;
    height: 16px;
    transform: scale(var(--s, 0.7));
    opacity: 0;
    border-radius: 50%;
    background: var(--active-inner);
  }
  .contactTable input[type=radio]:checked {
    --s: .5;
  }
}
